/**
 * Formula for determining extra attacks for BAB iteratives
 *
 * @type {string}
 */
export const iterativeExtraAttacks = "min(3, ceil(@bab / 5) - 1)";

/**
 * Formula for determining attack modifier for BAB iteratives
 *
 * @type {string}
 */
export const iterativeAttackModifier = "@attackCount * -5";

/**
 * Natural attack configuration.
 *
 * @todo Move more hardcoded definitions here.
 */
export const naturalAttacks = /** @type {const} */ ({
  secondary: {
    penalty: "-5",
  },
});

/**
 * Extra Attacks configurations
 *
 * @example
 * Custom additional option
 * ```js
 * ffd20.config.extraAttacks.turboMonk = {
 *   label: "Turbo Monk",
 *   iteratives: true,
 *   count: "floor(@bab / 3)",
 *   bonus: "@attackCount * -2",
 *   manual: true,
 * };
 * ```
 */
export const extraAttacks = /** @type {const} */ ({
  // Standard: BAB iteratives
  standard: {
    label: "FFD20.ExtraAttacks.Standard",
    iteratives: true,
    manual: false,
    formula: false,
  },
  // Advanced: BAB iteratives + manual extra attacks and custom formula
  advanced: {
    label: "FFD20.ExtraAttacks.Advanced",
    iteratives: true,
    manual: true,
    formula: true,
  },
  // Chained Monk's Flurry of Blows
  // Requires BAB override and class association
  flurry: {
    label: "FFD20.ExtraAttacks.Flurry",
    count: "ceil(@class.level / 7)",
    bonus: "-(@attackSetCount * 5)",
    attackName: "FFD20.ExtraAttacks.FlurryAttack",
    flavor: "FFD20.ExtraAttacks.FlurryFlavor",
    modToAll: "-(@fullAttack * 2)",
    iteratives: true,
    manual: false,
    formula: false,
  },
  // Unchained Monk's Flurry of Blows
  // Requires class association
  unflurry: {
    label: "FFD20.ExtraAttacks.UnFlurry",
    count: "floor((@class.level + 9) / 10)",
    //bonus: "0",
    flavor: "FFD20.ExtraAttacks.FlurryFlavor",
    attackName: "FFD20.ExtraAttacks.FlurryAttack",
    iteratives: true,
    manual: false,
    formula: false,
  },
  // Custom: No BAB iteratives but with manual extra attacks and custom formula
  custom: {
    label: "FFD20.ExtraAttacks.Custom",
    iteratives: false,
    manual: true,
    formula: true,
  },
});

