/**
 * Strict feat counting. If disabled, issues will be ignored.
 *
 * @experimental
 */
export const featCount = true;

/**
 * Token ruler configuration.
 *
 * @experimental
 */
export const tokenRuler = /** @type {const} */ ({
  grid: {
    immobile: { color: 0xff0000 }, // Immobile
    start: { color: 0x00ff00, alpha: 0.2 }, // Start square
    step: { color: 0x00aa00 }, // 5-foot step
    move: { color: 0x009900 }, // Single move
    move2: { color: 0x559900 }, // Double move
    move3: { color: 0x777700 }, // Triple move (run in heavy load/armor)
    run: { color: 0xaaaa00 }, // Normal run
    run2: { color: 0xbb5500 }, // Run feat
    exceed: { color: 0x990000 }, // Exceeding move limits
  },
  movement: {
    default: {
      max: 5,
      run: true,
      step: true,
    },
    // Special configuration for using swim skill instead of natural swim speed
    swimSkill: {
      factors: { 2: 4, 3: 5, 4: 5 },
      max: 2,
      run: false,
      step: false,
    },
    // Special configuration for using climb skill instead of natural climb speed
    climbSkill: {
      factors: { 2: 4, 3: 5, 4: 5 },
      max: 2,
      run: false,
      step: false,
    },
    crawl: {
      max: 1,
      run: false,
      step: false,
    },
  },
});

// Prepare sheet sections with data available later
// ... allowing module modification also.
Hooks.once("setup", () => {
  for (const [catKey, category] of Object.entries(sheetSections)) {
    for (const [sectKey, section] of Object.entries(category)) {
      section.category = catKey;
      section.id = sectKey;
      section.path = `${catKey}.${sectKey}`;

      section.label = game.i18n.localize(section.label);

      const iface = section.interface;
      if (typeof iface?.equip === "function") {
        iface.equip = iface.equip();
      }
    }
  }
});
