import { CheckboxFilter } from "./checkbox.mjs";

export class BuffTypeFilter extends CheckboxFilter {
  static label = "FFD20.Type";
  static type = "buff";
  static indexField = "system.subType";

  /** @override */
  prepareChoices() {
    this.choices = this.constructor.getChoicesFromConfig(ffd20.config.buffTypes);
  }
}
