import { CheckboxFilter } from "./checkbox.mjs";

export class ClassTypeFilter extends CheckboxFilter {
  static label = "FFD20.ClassType";
  static type = "class";
  static indexField = "system.subType";

  /** @inheritDoc */
  prepareChoices() {
    this.choices = this.constructor.getChoicesFromConfig(ffd20.config.classTypes);
  }
}

